.weather-info-container {
    display: flex;
    margin-bottom: 20px;
    justify-content: center;
    background-color: white;
    border-radius: 4px;
    padding: 20px;
    box-shadow: 0 1px 0 rgb(0 0 0 / 8%);
    font-size: 14px;
}

.weather-info-container img {
    width: 50px;
    height: 50px;
}
